import "core-js/modules/es.array.push.js";
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import { reactive, toRefs } from '@vue/reactivity';
export default {
  setup() {
    let router = useRouter();
    let store = useStore();
    let goBack = () => {
      router.go(-1);
    };
    let arry = reactive({
      List: null
    });
    arry.List = store.state.collec;
    let del = key => {
      store.commit("dels", key);
    };
    let goshop = key => {
      console.log(key);
      router.push({
        path: "/shopping",
        query: {
          key: key
        }
      });
    };
    return {
      goBack,
      ...toRefs(arry),
      del,
      goshop
    };
  }
};